  opacity: 1;
}

.loader {
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 100;
  width: 160px;
  height: 100px;
  transform: translate(-50%, -50%);
  pointer-events: none;
}
@media (max-width: 767px) {
  .loader {
    width: 106px;
    height: 32px;
  }
}
.loader.is-ending {
  animation: ending-loader 1.25s 1.5s ease both;
}
@media (max-width: 767px) {
  .loader.is-ending {
    animation: ending-loader-mobile 1.25s 1.5s ease both;
  }
}
.loader.no-animation {
  top: 5px;
  transform: translate(-50%, 0) scale(0.333);
  animation: none;
}
@media (max-width: 767px) {
  .loader.no-animation {
    top: 21px;
    transform: translate(-50%, 0) scale(0.45);
    animation: none;
  }
}
.loader__divider-left {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 3;
  width: 15px;
  height: 15px;
  transform: translate(-50%, -50%) rotate(45deg);
  transition: opacity 0.6s ease;
}
@media (max-width: 767px) {
  .loader__divider-left {
    width: 10px;
    height: 10px;
  }
}
.loader__divider-left::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 15px;
  height: 15px;
  background-color: #a7a9ac;
  transform: rotate(-45deg) translate(-80px) rotate(-45deg);
  animation: circle-left 2.5s ease both;
}
@media (max-width: 767px) {
  .loader__divider-left::before {
    width: 10px;
    height: 10px;
    transform: rotate(-45deg) translate(-40px) rotate(-45deg);
    animation: circle-left-mobile 2.5s ease both;
  }
}
.is-ending .loader__divider-left {
  opacity: 0;
}
.no-animation .loader__divider-left {
  opacity: 0;
}
.loader__divider-right {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 3;
  width: 15px;
  height: 15px;
  transform: translate(-50%, -50%) rotate(45deg);
  transition: opacity 0.6s ease;
}
@media (max-width: 767px) {
  .loader__divider-right {
    width: 10px;
    height: 10px;
  }
}
.loader__divider-right::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 15px;
  height: 15px;
  background-color: #a7a9ac;
  transform: rotate(-45deg) translate(80px) rotate(-45deg);
  animation: circle-right 2.5s ease both;
}
@media (max-width: 767px) {
  .loader__divider-right::before {
    width: 10px;
    height: 10px;
    transform: rotate(-45deg) translate(40px) rotate(-45deg);
    animation: circle-right-mobile 2.5s ease both;
  }
}
.is-ending .loader__divider-right {
  opacity: 0;
}
.no-animation .loader__divider-right {
  opacity: 0;
}
.loader__letter {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  transition: opacity 0.6s ease;
  width: 69px;
  height: 63px;
  display: flex;
}
.loader__letter svg {
  width: 100%;
  height: auto;
}
@media (max-width: 767px) {
  .loader__letter {
    width: 35px;
    height: 32px;
  }
}
.is-ending .loader__letter {
  opacity: 0;
}
.loader__logo {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  opacity: 0;
  transition: opacity 0.6s ease 0.6s;
  width: 478px;
  height: 147px;
  display: flex;
}
.loader__logo svg {
  width: 100%;
  height: auto;
}
@media (max-width: 767px) {
  .loader__logo {
    width: 239px;
    height: 74px;
  }
}
.is-ending .loader__logo {
  opacity: 1;
}

@keyframes circle-left {
  0% {
    transform: rotate(-45deg) translate(-80px) rotate(-45deg);
  }
  48% {
    transform: rotate(135deg) translate(-80px) rotate(-135deg);
  }
  52% {
    transform: rotate(135deg) translate(-80px) rotate(-135deg);
  }
  100% {
    transform: rotate(315deg) translate(-80px) rotate(-315deg);
  }
}
@keyframes circle-left-mobile {
  0% {
    transform: rotate(-45deg) translate(-40px) rotate(-45deg);
  }
  48% {
    transform: rotate(135deg) translate(-40px) rotate(-135deg);
  }
  52% {
    transform: rotate(135deg) translate(-40px) rotate(-135deg);
  }
  100% {
    transform: rotate(315deg) translate(-40px) rotate(-315deg);
  }
}
@keyframes circle-right {
  0% {
    transform: rotate(-45deg) translate(80px) rotate(-45deg);
  }
  48% {
    transform: rotate(135deg) translate(80px) rotate(-135deg);
  }
  52% {
    transform: rotate(135deg) translate(80px) rotate(-135deg);
  }
  100% {
    transform: rotate(315deg) translate(80px) rotate(-315deg);
  }
}
@keyframes circle-right-mobile {
  0% {
    transform: rotate(-45deg) translate(40px) rotate(-45deg);
  }
  48% {
    transform: rotate(135deg) translate(40px) rotate(-135deg);
  }
  52% {
    transform: rotate(135deg) translate(40px) rotate(-135deg);
  }
  100% {
    transform: rotate(315deg) translate(40px) rotate(-315deg);
  }
}
@keyframes ending-loader {
  0% {
    top: 50%;
    transform: translate(-50%, -50%);
  }
  100% {
    top: 5px;
    transform: translate(-50%, 0) scale(0.333);
  }
}
@keyframes ending-loader-mobile {
  0% {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  100% {
    top: 21px;
    transform: translate(-50%, 0) scale(0.45);
  }
}
